import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { usernameValidator, validator } from '../validators/validator';
import { ProductService } from '../shared/product.service';
import { RegisterOrLogin, S_CONSTANT } from '../bean/RegisterOrLogin';
import { MycookieService } from '../shared/mycookie.service';
import { ModalService } from '../shared/modal-service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ModalComponent } from '../modal/modal.component';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {
  /*  */
  headStyle: any;
  title: string;
  message: string;
  btnOK: string;
  btnNO: string;
  /*  */
  formModel: FormGroup;
  /* normal  */
  bsMRNormal: BsModalRef;
  /* error  */
  bsMRError: BsModalRef;

  constructor(
    public bsModalRef: BsModalRef,
    private fb: FormBuilder,
    private ps: ProductService,
    private router: Router,
    private cs: MycookieService,
    private modalService: BsModalService
  ) {
    this.formModel = fb.group({
      username: ["qqq@qq.com", [Validators.required, usernameValidator, Validators.maxLength(18), Validators.minLength(4)]],
      password: ["111111", [Validators.required, Validators.minLength(6)]],
    });
  }

  ngOnInit() { }

  onSubmit() {
    /*  */
    if (this.formModel.valid) {
      console.log('  ');
      console.log(this.formModel.value);
      /*   */
      this.bsModalRef.hide();
      this.openModalWithComponent();
      this.ps.getLoginInfor(this.formModel.value).subscribe(
        data => {
          /*  */
          if (data.isOK == 'Yes') {
            /*  */
            // this.modalRef = this.modalService.show(registerOK);
            let t = setTimeout(() => {
              /*  */
              this.bsMRNormal.content.closeModal(1);
              /* cookie */
               //this.cs.setStrCookie(RegisterOrLogin[1], 'Yes');
               var status = data.status;
              var id = data.username;
              console.log(S_CONSTANT[0]);
              console.log(id + " -- " + S_CONSTANT[0]);
              this.cs.setStrCookie("USER",  id);
              this.cs.setStrCookie(status, 'Yes');
              /*  */
              this.router.navigate(['/person']);
              /*   */
              this.ps.isLogin.emit(status);
              clearTimeout(t);
            }, 2000);
          } else {
            console.log('');
            console.log(data);
            /*  */
            this.bsMRNormal.content.closeModal(1);
            this.openModalError();
          }
        }
      );
      // 
    } else {
      console.log('');
    }
  }
  /* modal */
  openModalWithComponent() {
    /*  */
    const initialState = {
      title: '',
      message: null,
      headStyle: { 'head-warning': true },
      waiting: true
    };
    this.bsMRNormal = this.modalService.show(ModalComponent, {
      backdrop: "static",
      keyboard: false,
      initialState: initialState
    });
  }
  /*  */
  openModalError() {
    /*  */
    const initialState = {
      title: '',
      message: '',
      headStyle: { 'head-danger': true },
      waiting: false
    };
    let t = setTimeout(() => {
      this.bsMRError = this.modalService.show(ModalComponent, {
        backdrop: "static",
        keyboard: false,
        initialState: initialState
      });
      this.bsMRError.content.closeModal(2);
    }, 800);

  }
}



